<template>
  <div :class="{'editable-list-item-molecule':editable.id != selectedEditableId, 'editable-list-item-molecule-selected':editable.id == selectedEditableId}" v-on:click="editableSelected(editable)">

    <div class="editable-list-item-molecule__indicator">
      
      <div v-if="displaySquare" :class="displaySquare.class">
          <img v-if="displaySquare.image" :src="getAssetImage(displaySquare.image)"/>
          <label v-if="!displaySquare.image">{{editable.properties['0'].method}}</label>
      </div>

    </div>
    
    <div class="editable-list-item-molecule__caption">
      <label v-if="editable.type === EDITABLE_TYPES.DOCUMENT_LEVEL">All Requests</label>
      <label v-if="editable.type !== EDITABLE_TYPES.API && editable.type !== EDITABLE_TYPES.DOCUMENT_LEVEL">{{editable.properties['0'].name}}</label>
      <label v-if="editable.type === EDITABLE_TYPES.API">{{editable.properties['0'].path}}</label>
    </div>

    <div class="editable-list-item-molecule__toolbar" v-if="editable.type != EDITABLE_TYPES.DOCUMENT_LEVEL && editable.id == selectedEditableId" >
      <img v-on:click="deleteEditable(editable)" src="../../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" />
    </div>
  </div>

</template>

<script>
import {EDITABLE_TYPES} from '../../../../../../Common/VariousTypes'; 
import EditableListItemMolecule from './EditableListItemMolecule.hook'
const hook = new EditableListItemMolecule();

const mapping = [
  {type:EDITABLE_TYPES.DOCUMENT_LEVEL,image:"globe-solid.svg", class:"editable-list-item-molecule__indicator__all"},
  {type: EDITABLE_TYPES.ADMIN,image:"adminscript.svg",class:"editable-list-item-molecule__indicator__deploy"},
  {type: EDITABLE_TYPES.CRON,image:"clock-regular.svg",class:"editable-list-item-molecule__indicator__cron"},
  {type: EDITABLE_TYPES.MODULE,image:"puzzle-piece-solid.svg", class:"editable-list-item-molecule__indicator__module"},
  {type: EDITABLE_TYPES.SCHEMA,image:"schema.svg", class:"editable-list-item-molecule__indicator__schema"},
  {type: EDITABLE_TYPES.SCRIPT,image:"python-brands.svg", class:"editable-list-item-molecule__indicator__script"},
  {type: EDITABLE_TYPES.JSON,image:"file-js.svg", class:"editable-list-item-molecule__indicator__script"},
  {type: EDITABLE_TYPES.CSV,image:"file-csv.svg", class:"editable-list-item-molecule__indicator__script"},
  {type: EDITABLE_TYPES.TXT,image:"file-txt.svg", class:"editable-list-item-molecule__indicator__script"}, 
  {type: EDITABLE_TYPES.SQL,image:"file-sql.svg",class:"editable-list-item-molecule__indicator__script"},
  {type: EDITABLE_TYPES.HTML,image:"file-html.svg", class:"editable-list-item-molecule__indicator__script"},
  {type: EDITABLE_TYPES.API,class:"editable-list-item-molecule__indicator__api"}
  
]

export default {
  name: 'EditableListItemMolecule',
  beforeCreate() { hook.hook(this)},
  methods: {
    editableSelected(editable){
      hook.editableSelected(this,editable);
    },
    deleteEditable(editable){
      hook.deleteEditable(this,editable);
    },
    getAssetImage: hook.getAssetImage
  },
  props: { 
    editable:null
  },
  data() {
      return {
        EDITABLE_TYPES,
        mapping
      }
  },
  computed: {
    selectedEditableId(){
      return this.selectedItems?.editable?.id || -1;
    },
    displaySquare(){
      return mapping.find(m=>m.type == this.editable.type);
    },
    ...hook.getters("selectedItems","openedDocument")
  }
}

</script>

<style scoped lang="scss">

@mixin editable-list-item-molecule-mixin-eventbox($color){
  margin-left:20px;
  margin-top:5px;

  width: 60px;
  height:30px;
  border-radius: 5px;
  background-color:$color;
  display: table;
  text-align: center;
  font-size: 14px;
  color: white;
  label{
    width:100%;
    display: table-cell;
    vertical-align: middle;
  }
  i {
    font-size:20px;
    display: table-cell;
    vertical-align: middle;
  }
  img {
    padding-top:5px;
    height:20px;
  }
  cursor: pointer;  
  > * {
    cursor: pointer;
  }
}

@mixin editable-list-item-molecule-mixin(){
  
  height:40px;
  padding:5px;
  
  display: flex;
  transition: 0.2s;  


  cursor: pointer;  
  > * {
    cursor: pointer;
  }

  &__indicator{

    &__all{
      @include editable-list-item-molecule-mixin-eventbox(rgb(93, 192, 117));

    }

    &__schema {
      @include editable-list-item-molecule-mixin-eventbox(#745ba5);
    }

    &__script {
      @include editable-list-item-molecule-mixin-eventbox(#bebd6b);
    }

    &__module {
      @include editable-list-item-molecule-mixin-eventbox(#5ba599);
    }

    &__cron {
      @include editable-list-item-molecule-mixin-eventbox(#b76bbe);
    }

    &__deploy {
      @include editable-list-item-molecule-mixin-eventbox(#a55b61);
    }

    &__event {
      @include editable-list-item-molecule-mixin-eventbox(rgb(137, 160, 219));
    }
    &__api {
      @include editable-list-item-molecule-mixin-eventbox(rgb(79, 103, 163));
    }
  }

  &__caption {
    margin-left:20px;
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
  
    cursor: pointer;  
    > * {
      cursor: pointer;
    }

    label{
      display: inline-block;
      width:99%;
      white-space: nowrap; 
      overflow: hidden;
      text-overflow: ellipsis; 
    }
  }

  &__toolbar {

    display: flex;
    height: 100%;
    padding-top:10px;
    padding-right:10px;
    width:75px;

    img {
      cursor: pointer;
      height:16px;
    }
  }
}


.editable-list-item-molecule:hover {
  opacity: 0.5;
}

.editable-list-item-molecule {
 @include editable-list-item-molecule-mixin();  
}

.editable-list-item-molecule-selected {
 @include editable-list-item-molecule-mixin();
 background-color: rgb(148, 148, 148);
 color: black;
}

</style>
