import {EVENTS, REASONS} from '../../DesignSystem/UiResources/events';

export function switchEditable(atomicDesign, vue,editable){
    atomicDesign.emitGlobalEvent(EVENTS.DOCUMENT.BEFORE_SAVE,{reason:REASONS.BEFORE_SAVE.EDITABLE_SWITCH});
    atomicDesign.actions().patchSelectedItems({editable});
    atomicDesign.actions().setSelectedBlock(null);
    vue.openedDocument.selectedResources.selectedEditable = editable.id;
    atomicDesign.emitGlobalEvent(EVENTS.DOCUMENT.EDITABLE_SWITCHED,vue.selectedItems.editable);
}