import {AtomicDesign} from '../../../../../../Frontend/Core/AtomicDesign';
import {switchEditable} from '../../../../../../Frontend/Helpers/editableHelpers';

export default class EditableListItemMolecule extends AtomicDesign {

    editableSelected(vue,editable){
        switchEditable(this,vue,editable);
    }

    async deleteEditable(vue,editable){
        const result = await this.msgbox("Are you sure to delete this item?", this.MSGBOX_TYPE.YES_NO, "Confirm delete");
        if (result == this.MSGBOX_BUTTONS.YES){
            const openedDoc = vue.openedDocument;
            let newEditables = openedDoc.editables.filter(e=>e.id != editable.id);
            openedDoc.editables = newEditables
            this.actions().setOpenedDocument({result:openedDoc});
        }
    }
}